<template>
  <div>
    <Rlhead id="" title="登录" :flag="false"></Rlhead>
    <div class="login-box">
      <van-cell-group class="name-login">
        <van-field
          v-model="login.keys"
          clearable
          label="账号"
          right-icon="question-o"
          placeholder="请输入用户名/手机号"
          @click-right-icon="$toast('请输入2-6位字母或数字组成的用户名/11位手机号')"
          style="background:rgba(0,0,0,0.1)"
          class="name-box"
        />
      </van-cell-group>
      <van-cell-group class="pwd-login">
        <van-field
          v-model="login.password"
          type="password"
          clearable
          label="密码"
          right-icon="question-o"
          placeholder="请输入密码"
          @click-right-icon="$toast('请输入正确的密码')"
          style="background:rgba(0,0,0,0.1)"
          class="pwd-box"
        />
      </van-cell-group>
      <van-button round type="primary" size="large" @click="todoLogin">登录</van-button>

    </div>
  </div>
</template>

<script>
import Rlhead from "@/components/Rlhead.vue"
export default {
  data() {
    return {
      login: {}
    };
  },
  components:{
    Rlhead
  },
  methods:{
    todoLogin(){
      this.$axios.post("/vue/login",this.login).then(res=>{
                if(!!res.data.type){
                    sessionStorage.userInfo = JSON.stringify({
                        token:res.data.token,
                        username:this.login.keys
                    });

                    // this.changeUserInfo({
                    //     token:res.data.token,
                    //     username:this.login.keys
                    // });
                        this.$router.push({name:'mine'});
                }else{
                    sessionStorage.userInfo = JSON.stringify({
                        token:""
                    });
                }
            })
    }
  }

};
</script>

<style lang="scss" scoped>
.login-box {
  padding: 0 0.5rem;
  margin-top: 3rem;
}
.name-login,
.pwd-login {
  margin-bottom: 0.5rem;
  border-radius: 0.5rem ;
 text-align: center;
}
.name-box,.pwd-box{
  border-radius: 0.5rem ;
}
</style>